<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">Coupon 优惠券</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                优惠券。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a"></div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >

                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>

        </div>

    </div>
</template>

<script>
    export default {
        name: 'wltxtlinkhref',
        data() {
            return {

            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{

        }
    }
</script>
<style scoped>

</style>



